<template>
  <div>
    <el-row
      ><el-input
        placeholder=""
        v-model="query"
        class="search-input"
        style="width:23rem;margin-right:1rem;"
      >
      </el-input>
      <el-button type="primary" icon="el-icon-search" @click="fetch"
        ></el-button
      >
    </el-row>
    <el-table :data="list" stripe>
      <el-table-column
        prop="user.nickname"
        label=""
        width="130"
      ></el-table-column>
      <el-table-column prop="content" label=""></el-table-column>
      <el-table-column label="" width="150">
        <template slot-scope="scope">
          {{ scope.row["createdAt"] | formatDay }}
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            size="medium"
            type="danger"
            @click="handleDelete(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align:center;margin-top:1rem;">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="this.total"
        :page-size="limit"
        @prev-click="page -= 1"
        @next-click="page += 1"
        @current-change="handlePageIndex"
      >
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      query: "",
      page: 0,
      limit: 8,
      total: 0,
      list: []
    };
  },
  created() {
    this.fetch();
  },
  watch: {
    query() {
      if (this.query == "") {
        this.fetch();
      }
    },
    page() {
      this.fetch();
    }
  },
  methods: {
    async fetch() {
      const res = await this.$http({
        method: "GET",
        url: "/rest/video_commend",
        params: {
          match: {
            key: "content",
            val: this.query
          },
          populate: {
            path: "user"
          },
          limit: this.limit,
          page: this.page
        }
      });
      this.list = res.data;

      const res2 = await this.$http({
        method: "GET",
        url: "/rest/video_commend/count/page",
        params: {
          match: {
            key: "content",
            val: this.query
          }
        }
      });
      this.total = res2.data;
    },
    async handleDelete(item) {
      this.$confirm(``, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(async () => {
          await this.$http.delete(`/rest/video_commend/${item["_id"]}`);
          await this.$http.delete(
            `/rest/video_fabulou/remove_fabulou/${item["_id"]}`
          );
          this.fetch();
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    handlePageIndex(val) {
      this.page = val - 1;
    }
  }
};
</script>
