<template>
  <div>
    <el-card class="box-card" v-if="user">
      <div slot="header" class="clearfix">
        <span>{{ user.username }}</span>
        <el-button
          style="float: right; padding: 3px 0"
          type="text"
          @click.prevent="$router.back()"
          ></el-button
        >
      </div>
      <div class="text item">{{ user.nickname }}</div>
      <div class="text item">{{ user.email }}</div>
      <div class="text item">{{ user.birthday }}</div>
      <div class="text item">{{ user.tel }}</div>
      <div class="text item">{{ user.declaration }}</div>
      <div class="text item">
        <img v-if="user.avatar" :src="user.avatar" class="avatar" />
      </div>

      <div class="text item">
        {{ user.register_time | formatDate }}
      </div>
    </el-card>
  </div>
</template>
<script>
export default {
  props: {
    id: {}
  },
  created() {
    if (this.id) {
      this.fetch();
    }
  },
  data() {
    return {
      user: {}
    };
  },
  methods: {
    async fetch() {
      const res = await this.$http.get(`rest/user/${this.id}`);
      this.user = res.data;
      console.log(this.user);
    },
    async save() {
      this.$router.push({
        name: "adminList"
      });
    }
  }
};
</script>
