      </el-aside>

      <el-container>
        <el-header style="text-align: right; font-size: 12px">
          <span>{{ username }}</span>
          <el-button type="danger" plain size="small" @click="loginOut"
            ><i class="el-icon-switch-button"></i> </el-button
          >
        </el-header>
        <el-main> <router-view :key="$route.path"></router-view></el-main>
      </el-container>
