</template>
<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState({
      username: state => state.user.username
    })
  },
  methods: {
    loginOut() {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          window.localStorage.removeItem("token");
          this.$router.push({
            name: "login"
          });
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  }
};
</script>
<style>
.el-header {
  background-color: #b3c0d1;
